@import "./../../common/styles/sass/variables";

.skillsBlock {
  padding: 80px 0 50px;
  border-top: 1px solid #323641;
  @media (max-width: 526px) {
    padding: 65px 0 30px 0;
  }

  .skills {
    display: flex;
    flex-wrap: wrap;
    @media (max-width: 965px) {
      justify-content: space-between;
    }
  }
}





